import React, { useState, useEffect } from 'react';
import { MapContainer, TileLayer, Marker, Polyline, useMap,Popup } from 'react-leaflet';
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import customMarkerIcon from '../../assets/car.svg';
import huyHieu from '../../assets/huyhieu.svg'
import * as Realm from "realm-web";
import dataTable from "../../assets/data";

const app = new Realm.App({ id: process.env.REACT_APP_MONGODB_APP_ID });

const MapView = () => {
  const [currentPosition, setCurrentPosition] = useState(null);
  const [destination, setDestination] = useState([51.515, -0.1]);
  const [route, setRoute] = useState([]);
  const [isDrawing, setIsDrawing] = useState(false);

  const customIcon = L.icon({
    iconUrl: customMarkerIcon,
    iconSize: [38, 38],
    iconAnchor: [19, 38],
    popupAnchor: [0, -38]
  })

  const huyHieuIcon = L.icon({
    iconUrl: huyHieu,
    iconSize: [38, 38],
    iconAnchor: [19, 38],
    popupAnchor: [0, -38]
  })

  useEffect(() => {
    const handlePositionUpdate = (position) => {
      const { latitude, longitude } = position.coords;
      const newPosition = [latitude, longitude];
      setCurrentPosition(newPosition);
      if (isDrawing) {
        setRoute((prevRoute) => [...prevRoute, newPosition]);
      }
    };

    const handleError = (error) => {
      console.error("Error getting position: ", error);
    };

    navigator.geolocation.getCurrentPosition(handlePositionUpdate, handleError, {
      enableHighAccuracy: true,
      timeout: 1000,
      maximumAge: 0,
    });

    const watchId = navigator.geolocation.watchPosition(handlePositionUpdate, handleError, {
      enableHighAccuracy: true,
      timeout: 1000,
      maximumAge: 0,
    });

    return () => navigator.geolocation.clearWatch(watchId);
  }, [isDrawing]);

  const handleStartStop = async() => {
    if (isDrawing) {
      try {
        const mongodb = app.currentUser.mongoClient("mongodb-atlas");
        const collection = mongodb.db("hanhtrinhxanh").collection("recordRoad");
        await collection.insertOne({
          route: route
        });
        alert("Ghi d liu thnh cng!");
      } catch(error) {
        alert("Li thm d liu: ", error);
      }
    }
    setIsDrawing(!isDrawing);
  };

  useEffect(() => {
    // const getRoute = async () => {
    //   const user = await app.logIn(Realm.Credentials.anonymous());
    //   const mongodb = user.mongoClient("mongodb-atlas");
    //   const collection = mongodb.db("hanhtrinhxanh").collection("recordRoad");
    //   const locationsData = await collection.find();  
    //   console.log(locationsData);
    // }

    // getRoute();
    console.log(dataTable);
  }, []);

  const MapCenterSetter = () => {
    const map = useMap();

    useEffect(() => {
      if (currentPosition && isDrawing) {
        map.setView(currentPosition, 13);
      }
    }, [currentPosition, map, isDrawing]);

    return null;
  };

  return (
    <div>
      {/* <button onClick={handleStartStop}>
        {isDrawing ? 'Dng' : 'Bt u'}
      </button> */}
      <MapContainer center={currentPosition || [10.7727155,106.6304589]} zoom={13} style={{ height: '75vh' }}>
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        />
        {currentPosition && <Marker position={currentPosition} icon={customIcon} />}
        {dataTable.map(location => (
          <Marker
            key={location.name}
            position={[location.position.lat, location.position.lng]}
            icon={huyHieuIcon}
          >
            <Popup>
              <div className="flex flex-col gap-4">
                <div className="flex items-center gap-4">
                  <h3>{location.name}</h3>
                  <p>{location.job}</p>
                </div>
                <div className="w-full max-w-sm h-auto object-cover rounded">
                  {location.image && <img src={location.image} alt="Location" style={{width: '100%', maxWidth: '100px',maxHeight:'100px'}} />}
                  {location.pImage && <img src={location.pImage} alt="Location" style={{width: '100%', maxWidth: '100px',maxHeight:'100px'}} />}
                </div>
              </div>
            </Popup>
          </Marker>
        ))}
        <Polyline positions={route} color="blue" />
        <MapCenterSetter />
      </MapContainer>
    </div>
  );
};

export default MapView;